.home-main{
    background-image: url("../images/wall3.jpg");
    background-position: center;
    background-repeat: no-repeat;
    height: 100vmin;
    background-color: hsla(0, 0%, 0%, 0.2);
    background-blend-mode: overlay;
    display: flex;
    align-items: center;
}

.home-main::after {
    content: "";
    width: 100vw;
    height:100vmin;
    background-color: #000000cd;
    position: absolute;
    /* top: 71px; */
    top: 0;
    left: 0;
    clip-path: polygon(67% 0, 100% 0%, 100% 100%, 29% 100%);
    max-width: 100%;
}

.home-text{
    margin-left: 7.5%;
    width:60%;
    z-index: 1;
}

.home-text > h1 {
    color: white;
    font-size:4vmax;
}

.home-text > h1 > span {
    color: #00ffb7;
}

.home-text > p {
    font-size:1.4vmax;
    color:whitesmoke
}

.home-text-btn {
    padding: 10px 20px;
    background: none;
    border: 1px solid white;
    transition:all 0.4s;
    font-size: large;
    margin-top: 20px;
    color: whitesmoke;
    transition: 0.5s ease;
}

.home-text-btn:hover {
    border: 1px solid #00ffb7

;
    background: #00ffb7

;
    color: black;
    cursor: pointer;
}

.featured-product {
    width: 80%;
    margin: auto;
}

.featured-product-txt{
    text-align: center;
    font-size: 1.4vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.28);
    width: 25vmax;
    margin: 5vmax auto;
    margin-bottom: 2vmax;
    color: rgba(0, 0, 0, 0.8);
}

.product-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.searchbar {
    font-size: 1.2vmax;
    cursor: pointer;
}

@media screen and (max-width: 600px) {
    .featured-product {
        width: 100%;
    }
    .home-text {
        width:80%;
    }
    .home-text-btn {
        padding: 5px 10px;
        font-size: small;
    }

    .featured-product>h2 {
        font-size: large;
    }

    .product-container> a {
        width: 20%;
        margin: 1vmax .5vmax;
    }

    .product-container>a>p {
        font-size: 1.1vmax;
    }

    .product-container>a>span {
        font-size: 1vmax;
        margin-left:1vmax;
    }
}